import React from 'react';
import Ad from "../ad/Ad";

import imgOne from '../../img/1.jpg';
import imgTwo from '../../img/2.jpg'
import imgThree from '../../img/3.jpg'
import imgFour from '../../img/4.jpg'
import imgFive from '../../img/5.jpg'

const Ads = () => {
  const imgs = [imgOne, imgTwo, imgThree, imgFour, imgFive];

  return (
    <div className="ads">
        {imgs.map((src, index)=> {
            return (
              <Ad key={index} src={src}/>
            )
        })}
      {imgs.map((src, index)=> {
        return (
          <Ad key={index} src={src}/>
        )
      })}
      {imgs.map((src, index)=> {
        return (
          <Ad key={index} src={src}/>
        )
      })}
      {imgs.map((src, index)=> {
        return (
          <Ad key={index} src={src}/>
        )
      })}
    </div>
  )

}

export default Ads;