import React from 'react';

const Ad = (props) => {
  const {src} = props;

  return (
    <div className="ad">
      <div className="ad__container">
        <a className="ad__link" href='/'>
          <img className="ad__image" src={src} alt="Картинка объявления"/>
        </a>
      </div>
    </div>
  )

}

export default Ad;