import React from 'react';
import Header from "../header/Header";
import Footer from "../footer/Footer";
import Search from "../search/Search";

const Layout = (props) => {
  return (
    <div className="layout">
      <div className="background">
        <Header/>
        <Search/>
      </div>
      {props.children}
      <Footer/>
    </div>
  )

}

export default Layout;